import React, { useRef, useState, useEffect } from 'react';
import Layout from '../components/layout';
import styled from 'styled-components';
import SectionTitle from '../components/shared/section-titles';
import { TitleH1 } from './magazini';
import StoreIcons from '../components/shared/store-icons';
import constants from '../constants/netsurf-play';
import { BgWrapper } from '../components/shared/components';
import { graphql } from 'gatsby';
import SEO from '../components/seo';
import BrowserDetection from 'react-browser-detection';
import ScrollAnimation from 'react-animate-on-scroll';
import meta from '../constants/meta-descriptions';
import nsVideo from '../images/netsurfplay/netsurf-play.mp4';
import '../styles/common.css';



const StoreIconsWrapper = styled.div`
  background: #FFFFFF;
  border-radius: 15px;
  box-shadow: 0px 0px 36px rgba(17,41,70,0.25);
`;

export default ({ data }) => {
  const [screenSize, setScreenSize] = useState(0);

  useEffect(() => {
    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);
    return () => {
      window.removeEventListener('resize', checkScreenSize);
    };
  }, [])

  const checkScreenSize = () => {
    setScreenSize(window.innerWidth);
  }

  const videoRef = useRef(null);
  const [playButtonClassName, setPlayButtonClassName] = useState('play-button');

  return (
    <>
      <BgWrapper
        bg={data.bg.childImageSharp.fluid.src}
        position={'bottom'}
      >
        <Layout>
          <SEO
            title={meta.netsurfplay.title}
            description={meta.netsurfplay.metaDescription}
          />
          <div style={{ paddingTop: '12rem' }}>
            <SectionTitle
              undertext='Гледай любимата си телевизия в реално време и си плащай сметките с един „цък”.'>
              <TitleH1>
                NetSurf Play:<br />
                Удобство и забавление в една човешка длан
            </TitleH1>
              <hr className='center' />
            </SectionTitle>

            <BgWrapper
              bg={data.videoBg.childImageSharp.fluid.src}
              color={'transparent'}
            >
              <div className='container'>

                <div className='video-wrapper'>
                  <video
                    ref={videoRef}
                    width='100%'
                    height='auto'
                    autoPlay={!(screenSize <= 991)}
                    muted={true}
                    loop={true}
                    controls={false}
                  >
                    <source src={nsVideo} type="video/mp4" />
                  </video>

                  {(screenSize <= 991) && (
                    <div
                      className={playButtonClassName}
                      onClick={(e) => {
                        videoRef.current.play();
                        setPlayButtonClassName('play-button playing');
                      }}></div>
                  )}
                </div>
              </div>
            </BgWrapper>
            <div className='container'>
              <div className='d-flex justify-content-center align-items-center flex-column w-100 mt-5'>
                <ScrollAnimation
                  animateIn='fadeIn'
                  animateOnce={true}
                >
                  <StoreIconsWrapper className='text-center mt-5 p-3'>
                    <h2>Свали приложението от:</h2>
                    <StoreIcons />
                  </StoreIconsWrapper>
                </ScrollAnimation>
              </div>
            </div>
            <div className='row d-flex justify-content-center my-5'>
              <div className='col-12 col-lg-11 col-xl-9 row d-flex justify-content-center'>
                {constants.features.map(({ image, title, text }, index) => (
                  <div className='col-12 col-sm-6 col-lg-4 text-center px-md-5 my-3 my-sm-5' key={index}>
                    <ScrollAnimation
                      animateIn='fadeIn'
                      animateOnce={true}
                    >
                      <div className='d-flex justify-content-center align-items-center' style={{ minHeight: '100px' }}>
                        <img src={image} className='img-fluid' alt={title} />
                      </div>
                      <div className='px-sm-4'>
                        <h3>{title}</h3>
                        <p>{text}</p>
                      </div>
                    </ScrollAnimation>
                  </div>
                ))}
              </div>
            </div>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <div className='container d-flex justify-content-center align-items-center mb-5'>
                <StoreIconsWrapper className='text-center mt-5 p-3'>
                  <h2>Свали приложението от:</h2>
                  <StoreIcons />
                </StoreIconsWrapper>
              </div>
            </ScrollAnimation>

          </div>
        </Layout>
      </BgWrapper>
    </>
  )
}

export const query = graphql`
  query NetSurfPlay {
    bg: file(relativePath: {eq: "netsurfplay/bg.png"}) {
      childImageSharp {
        fluid (maxWidth: 2000) {
          src
        }
      }
    }
    videoBg: file(relativePath: {eq: "netsurfplay/video-bg.png"}) {
      childImageSharp {
        fluid (maxWidth: 2000) {
          src
        }
      }
    }
  }
`; 